.list {
    margin: 8px auto;
    width: 80%;
    display: flex;
    flex-direction: column;
    align-items: flex-start; /* Önceki kodlardan farklı olarak align-items: flex-start */
    justify-content: center;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1), 0 8px 16px rgba(0, 0, 0, 0.1);
    transition: 0.3s ease-in-out;
    position: relative;
    overflow: hidden;
    position: relative;
  }
  
  .list:hover {
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2), 0 16px 32px rgba(0, 0, 0, 0.2);
  }
  

  .position {
    font-weight: 700;
    font-size: 1.8rem;
    margin-bottom: 16px;
  }
  
  .position a {
    font-weight: 500;
    text-decoration: none;
  }
  
  .position span {
    color:  #d8bb3d;
    font-weight: 500;
  }
  .position .time {
    color: black;
    font-size: 1.2rem;
    font-weight: 500;
  }

  .position .company {
    color: black;
    font-weight: 500;
  }
  
  .position .ta {
    color: black;
    font-size: 1.2rem;
    font-weight: 500;
  }
  
  .work {
    font-size: 1.2rem;
    width: 100%;
    font-weight: 400;
    margin-bottom: 16px;
  }
  
  .experience {
    margin-top: 16px;
    margin-bottom: 16px;
  }
  
  .title {
    font-weight: 800;
    font-size: 2rem;
    margin-bottom: 2.5rem;
    margin-top: 2.5rem;
    text-align: center;
    color:  #333;
  }
  
  .details {
    margin: 0 auto;
    position: relative;
  }
  
  .details ul {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: start;
    justify-content: space-between;
    margin-left: 20px; /* Kutucukların sol tarafındaki mesafe */
  }
  
  @media screen and (max-width: 768px) {
    .list {
        width: 100%; /* veya istediğin başka bir değer */
    }

    .details {
        width: 100%; /* veya istediğin başka bir değer */
    }
}