:root {
  --bg-dark: #0f141e;
  --bg-light: #f9f9f9;
  --tw-bg-opacity: 1;
  --bg-light2: rgb(245 245 245 / var(--tw-bg-opacity));
  --dark-color: rgba(15, 20, 30, 0.8);
  --danger-color: #ea4343;
}

*,
::before,
::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  background-color: var(--bg-light);
  font-family: "Josefin Sans", sans-serif;
  color: #000;
  font-size: 23px;
  font-weight: 600;
  line-height: 1.5;
}

.container {
  max-width: full;
  margin-left: 7.5rem;
  margin-right: 7.5rem;
  overflow: auto;
  padding: 0 20px;
}

ul {
  list-style-type: none;
}

a {
  text-decoration: none;
  color: #000;
}

p {
  margin: 10px 0;
  font-weight: 500;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Josefin Sans', sans-serif;
}

.reveal {
  transform: translateY(200px);
  opacity: 0;
  transition: all 2s ease;
}

.show {
  transform: translateY(0px);
  opacity: 1;
}

.grid {
  display: grid;
  grid-template-columns: 3fr 4fr;
  justify-content: space-between;
  align-items: center;
  gap: 100px;
  height: 100%;
  padding: 40px 0;
}

.active {
  color: var(--danger-color);
}

.scroll-top {
  position: sticky;
  bottom: 20;
  right: 20;
  z-index: 100;
}


/* Responsive tasarım için media query ekleyin */
@media screen and (max-width: 768px) {

  .list, .list2 {
    width: 100%;
    max-width: 100%;
  }
}
/* Ayrıca, her iki bileşenin içindeki listelerin genişliklerini ayarlamak için şu CSS'i ekleyebilirsiniz */
.list, .list2 {
  width: 100%;
  max-width: 1000px;
}

@media screen and (max-width: 768px) {
  body {
    text-align: justify;
  }

  .grid {
    grid-template-columns: 1fr;
  }

  /* Responsive tasarım için eklenen stil kodları */
  .home-title h2 {
    font-size: 1.5rem;
    margin-bottom: 0.3rem;
  }

  .text-wrapper {
    font-size: 1rem;
  }

  .text-wrapper p {
    font-size: 2rem;
  }

  .links {
    font-size: 1rem;
    margin-top: 10px;
  }

  .link a::after {
    bottom: -3px;
  }

  .divider {
    font-size: 1.2rem;
  }

  .scroll a span {
    font-size: 1.2rem;
    bottom: -50px;
    width: 160px;
    height: 36px;
    padding: 8px;
  }

  .scroll a span::before {
    top: 3px;
    left: 80%;
    height: 15px;
  }

  .circle {
    width: 240px;
    height: 240px;
  }

  .circ img {
    width: 300px;
    height: 300px;
  }

  .content {
    inset: 20px;
  }

  .content a {
    font-size: 1rem;
    padding: 8px 8px;
  }
  /* Responsive tasarım için eklenen stil kodları sonu */
}
