.list2 {
    margin: 8px auto;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1), 0 8px 16px rgba(0, 0, 0, 0.1);
    transition: 0.3s ease-in-out;
    overflow: hidden;
  }
  
  .list2:hover {
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2), 0 16px 32px rgba(0, 0, 0, 0.2);
  }
  
  
  .position2 {
    font-weight: 700;
    font-size: 1.8rem;
    margin-bottom: 8px;
    color: black; /* Renk eklendi */
  }
  
  .position2 .field {
    font-weight: 500;
    font-size: 1.2rem;  
    color: black;
  }
  .position2 a {
    font-weight: 600;
    text-decoration: none;
    color:  #3498db; /* Link rengi eklendi */
  }
  
  .position2 span {
    font-weight: 700;
    color: black; 
  }

  .position2 .et {
    color:   #d8bb3d;
    font-weight: 500;
  }

  .position2 .et .school {
    font-weight: 500;
    font-size: 1.5rem;
    color: black;
  }
  
  .position2 .time {
    font-weight: 500;
    font-size: 1.2rem;
  }
  
  .position2 .school {
    font-size: 1.2rem;
    font-weight: 600;
  }

  

  
 .position2 .work2 {
    font-size: 1.2rem;
    width: 100%;
    font-weight: 400;
  }
  
  .experience2 {
    margin-top: 16px;
    margin-bottom: 16px;
  }
  
  .title2 {
    font-weight: 800;
    font-size: 2rem;
    margin-bottom: 2.5rem;
    margin-top: 2.5rem;
    text-align: center;
    color:  #333;
  }
  
  .details2 {
    margin: 0 auto;
    position: relative;
  }
  
  .details2 ul {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: start;
    justify-content: space-between;
    margin-left: 20px; /* Kutucukların sol tarafındaki mesafe */
  }
  

  
  @media screen and (max-width: 768px) {
    .list2 {
        width: 100%; /* veya istediğin başka bir değer */
    }

    .details2 {
        width: 100%; /* veya istediğin başka bir değer */
    }
}